
/* 

Primary-yellow: #f6b93b

*/

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
	font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
	height: 100%;
}

a{
    color: #fff;
    text-decoration: none;
}

/*Utility Classes*/

.btn {
    display: inline-block;
    text-decoration: none;
    color: #eee;
    font-size: 0.6em;
    border: 2px solid #f6b93b;
    border-radius: 100px;
    padding: 15px 40px;
    margin-top: 30px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    cursor: pointer;
}

.btn:hover {
    background-color: #f6b93b;
    color: #333;
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
    box-shadow: 0 10px 20px rgba(255, 230, 0, 0.2);
}

.btn:active {
    -webkit-transform: translateY(-1px);
            transform: translateY(-1px);
    box-shadow: 0 5px 10px rgba(255, 230, 0, 0.2);
}

.btn-primary {
    background-color: #f6b93b;
    color: #333;
}

.active {
    padding: 20px;
    border-bottom: 2.5px solid #f6b93b;
}


/*Navbar*/

nav {
    color: #fff;
    /* background: linear-gradient(to right bottom, #2424247e, #2424247e),url(../images/showcase-bg.jpg) no-repeat; */
    /* background: linear-gradient(to right bottom, #1f1f1fbd, #000000bd),url(../images/cloud-bg.svg) no-repeat; */
    background: -webkit-gradient(linear, left top, right bottom, from(#161616dc), to(#161616dc)),url(/static/media/cloud-bg.c2efe043.svg) no-repeat;
    background: -webkit-linear-gradient(left top, #161616dc, #161616dc),url(/static/media/cloud-bg.c2efe043.svg) no-repeat;
    background: linear-gradient(to right bottom, #161616dc, #161616dc),url(/static/media/cloud-bg.c2efe043.svg) no-repeat;
    height: 100vh;
    background-color: #191C2F;
    background-position: center;
    background-size: cover;
    /* background-color: rgba(182, 182, 182, 0.37); */
    width: 100%;
}

#logo {
    padding-top: 20px;
}

#topbar{
    /* background-color: #1C1C1F; */
    color: #fff;
}

#topbar ul {
    
    width: 100%;
    max-width: 1000px;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
}

#topbar ul li {
    font-size: 1.4em;
    font-weight: 500;
    margin-left: auto;
    display: inline-block;
    padding: 25px;
}

/*Top navbar Showcase*/

#showcase {
    height: 88.86%;
    /* background-color: #81ecec; */
    display: grid;
    grid-template-columns: 7fr 5fr;
    margin: 0 auto;
}

#showcase-text {
    font-size: 3.5em;
    text-align: center;
    margin: 20% auto;
}

#showcase-img {
    /* background-color: #e74c3c; */
    text-align: center;
}

#project{
	display: grid;
	grid-template-rows: 1fr;
	grid-row-gap: 10px;
}

.project-row{
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-column-gap: 10px;
}

.project-intro{
	padding: 15%;
    text-align: center;
	/* background-color: #f6b93b; */
}

.project-intro h2{
	padding: 2.5%;
}

.project-intro p{
	padding: 2.5%;
}

.project-img img{
    border: 2px solid rgba(51, 51, 51, 0.13);
    padding: 20px;
    width: 100%;
    height: 100%;
}

/*Skills Section*/

#skill-title{
	text-align: center;
	font-size: 3em;
	font-weight: 600;
	margin: 20px auto;
}

#skills-section{	
	display: grid;
	grid-template-rows: 1fr;
}

.skills-row{
	padding: 30px;
	display: grid;
	grid-template-columns: 1fr 1fr 1fr;
	text-align: center;
}

.skills{
	margin: 0 auto;
}

/*Footer*/

footer{
    background-color: #333;
    width: 100%;
    color: #fff;
    padding: 20px 20px 10px 20px;
    text-align: center;
}

#social{
    margin: 20px 0px;
}

.social-icon{
    display: inline-block;
}

#copyright-credit{
    margin: 10px 0;
}

#heart{
    font-size: 1.6em;
    padding: 3px;
    color: red;
}

/*About Section*/

#about{
}

#about-img img{
    display: block;
    margin: 40px auto;
    width: 300px;
    height: 300px;
    border-radius: 10%;
}

#about-text{
    font-size: 1.3em;
    padding: 3% 10%;
}

#about-social{
    padding: 20px;
    margin: 0 auto;
    text-align: center;
}

#about-social a{
    margin: 20px;
}
